body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
padding: 0;
margin: 0;
box-sizing: border-box;
}

html, body {
    background-color: rgb(71, 132, 132);
}

.App {
display: flex;
flex-wrap: wrap;
align-items: center;
justify-content: center;
padding-top: 15px;
font-family: 'Heebo', sans-serif;
}

.logo-contenedor {
width: 100%;
display: flex;
align-items: center;
justify-content: center;
margin: 20px;
}

logo {
    height: 50px;
}

.contendedor-principal {
    height: 550px;
    min-width: 600px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
button {
    width: 250px;
    height: 80px;
    font-size: 30px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif,Roboto, "Helvetica Neue";
    font-weight: bold;
    color: rgba(48, 14, 14, 0.409);
    border: 2px solid #fff;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
}

button:hover {
    background-color: rgba(10, 13, 185, 0.604);
}

.boton-click {
    background-color: rgb(132, 183, 200);
}

.boton-reiniciar {
    background-color: rgb(132, 183, 200);
}
.contador {
    min-width: 300px;
    height: 300px;
    font-size: 160px;
    padding: 25px;
    color:antiquewhite;
    display: flex;  
    align-items: center;
    justify-content: center;
    border: 8px solid black;
    margin-bottom: 15px;
    border-radius: 15px;
}
