button {
    width: 250px;
    height: 80px;
    font-size: 30px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif,Roboto, "Helvetica Neue";
    font-weight: bold;
    color: rgba(48, 14, 14, 0.409);
    border: 2px solid #fff;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
}

button:hover {
    background-color: rgba(10, 13, 185, 0.604);
}

.boton-click {
    background-color: rgb(132, 183, 200);
}

.boton-reiniciar {
    background-color: rgb(132, 183, 200);
}